<template>
    <div>
        <div class="crumbs" style="padding-left:20px;">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-s-operation"></i> 批阅逻辑
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container" style="border-bottom:0;">
            <div class="form">
                <div class="form-top">
                    <el-radio-group size="small" v-model="choosed">
                      <el-radio-button label="加工表面"></el-radio-button>
                      <el-radio-button label="工序规则"></el-radio-button>
                    </el-radio-group>
                    <el-button v-if="choosed=='加工表面'" @click="editVisible=true" class="fr" type="primary" size="small">新建表面</el-button>
                    <el-button v-else class="fr" type="primary" @click="editVisiblerule=true" size="small">新建规则</el-button>
                </div>
                <div class="cont">
                    <div class="onebg"  v-loading="loading" v-show="choosed=='加工表面'">
                      <el-table
                        :data="biaolist"
                        style="width: 100%;border:1px solid #f1f1f1;border-bottom:0;">
                        <el-table-column
                          label="表面ID"
                          prop="surfaceId"
                          width="180">
                        </el-table-column>
                        <el-table-column
                          label="类型">
                          <template slot-scope="scope">
                            {{biaoptions[scope.row.type-1].name}}
                          </template>
                        </el-table-column>
                        <el-table-column
                          label="名称"
                          prop="name">
                        </el-table-column>
                        <el-table-column width="200" label="操作">
                          <template slot-scope="scope">
                            <el-button
                              size="mini"
                              @click="editbiao(scope.row)">编辑</el-button>
                            <el-button
                              size="mini"
                              type="danger"
                              @click="delbiao(scope.row.surfaceId)">删除</el-button>
                          </template>
                        </el-table-column>
                      </el-table>
                    </div>

                    <div class="onebg"  v-loading="loading" v-show="choosed=='工序规则'">
                        <el-table
                        :data="ruleItems"
                        style="width: 100%;border:1px solid #f1f1f1;border-bottom:0;">
                        <el-table-column
                          type="index"
                          label="序号"
                          width="180">
                        </el-table-column>
                        <el-table-column
                          label="规则">
                          <template slot-scope="scope">
                            {{ruleoptions[scope.row.rulesType-1].name}}
                          </template>
                        </el-table-column>
                        <el-table-column
                          label="规则内容"
                          prop="rulesDetails">
                        </el-table-column>
                        <el-table-column width="200" label="操作">
                          <template slot-scope="scope">
                            <el-button
                              size="mini"
                              @click="editrule(scope.row)">编辑</el-button>
                            <el-button
                              size="mini"
                              type="danger"
                              @click="delrule(scope.row.id)">删除</el-button>
                          </template>
                        </el-table-column>
                      </el-table>
                    </div>

                </div>
            </div>
        </div>
        <!-- 新建规则 -->
        <el-dialog title="新建规则" class="datas-bg" :visible.sync="editVisiblerule" width="380px">
            <el-select style="width:100%;" class="margin10" v-model="newruletype" size="small" placeholder="请选择规则类型">
              <el-option
                v-for="item in ruleoptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
            <p v-if="newruletype==1" style="color:#999;font-size:12px;margin-top:4px;"><span style="color:red;">* </span>请将加工表面ID的优先级按照加工优先级次序输入，同组ID以“,”间隔</p>
            <p  v-if="newruletype==2" style="color:#999;font-size:12px;margin-top:4px;"><span style="color:red;">* </span>请以 表面ID-加工方法ID 的形式输入，不同规则以“;”间隔。加工方法ID：车削-a；铣削-b；刨-c；磨-d；钻-e；铰-f</p>
            <el-input  size="small" style="margin:10px 0 20px;" type="textarea" placeholder="请输入规则" rows="5" v-model="newtext"></el-input>
            <el-button type="primary" @click="newrule" style="display:block;margin:0 auto;" size="small">确 定</el-button>
        </el-dialog>
        <!-- 编辑规则 -->
        <el-dialog title="编辑规则" class="datas-bg" :visible.sync="editVisiblerule2" width="380px">
            <el-select style="width:100%;" class="margin10" v-model="modifyruleItem.rulesType" size="small" placeholder="请选择规则类型">
              <el-option
                v-for="item in ruleoptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
            <p v-if="modifyruleItem.rulesType==1" style="color:#999;font-size:12px;margin-top:4px;"><span style="color:red;">* </span>请将加工表面ID的优先级按照加工优先级次序输入，同组ID以“,”间隔</p>
            <p  v-if="modifyruleItem.rulesType==2" style="color:#999;font-size:12px;margin-top:4px;"><span style="color:red;">* </span>请以 表面ID-加工方法ID 的形式输入，不同规则以“;”间隔。加工方法ID：车削-a；铣削-b；刨-c；磨-d；钻-e；铰-f</p>
            <el-input  size="small" style="margin:10px 0 20px;" type="textarea" placeholder="请输入规则" rows="5" v-model="modifyruleItem.rulesDetails"></el-input>
            <el-button type="primary" @click="modifyrule" style="display:block;margin:0 auto;" size="small">确 定</el-button>
        </el-dialog>
        <!-- 新建表面 -->
        <el-dialog title="新建表面" class="datas-bg" :visible.sync="editVisible" width="380px">
            <el-input class="margin10" v-model="newname" size="small" placeholder="请输入表面名称"></el-input>
            <el-select style="margin:10px 0 20px;width:100%;" class="margin10" v-model="newtype" size="small" placeholder="请选择表面类型">
              <el-option
                v-for="item in biaoptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
            <el-button type="primary" @click="newbiao" style="display:block;margin:0 auto;" size="small">确 定</el-button>
        </el-dialog>
        <!-- 编辑表面 -->
        <el-dialog title="新建表面" class="datas-bg" :visible.sync="editVisible2" width="380px">
            <el-input class="margin10" v-model="modifyItems.name" size="small" placeholder="请输入表面名称"></el-input>
            <el-select style="margin:10px 0 20px;width:100%;" class="margin10" v-model="modifyItems.type" size="small" placeholder="请选择表面类型">
              <el-option
                v-for="item in biaoptions"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
            <el-button type="primary" @click="modifybiao" style="display:block;margin:0 auto;" size="small">确 定</el-button>
        </el-dialog>
    </div> 
</template>

<script>
import {fetchListrules, fetchFinishedsurface, fetchFinishedsurfaceadd, fetchFinishedsurfaceupdate, fetchFinishedsurfacedel, fetchAddrule, fetchUpdaterule, fetchDeleterule} from "../../axios/requests";
export default {
    name: 'course',
    data() { 
        return {
          loading:false,
          projectId:'',
          choosed:'加工表面',
          biaolist:[],
          biaoptions:[{
                id:1,
                name:'平面'
            },{
                id:2,
                name:'外圆'
            },{
                id:3,
                name:'孔'
            }
          ],
          //新建表面
          editVisible:false,
          newtype:'',
          newname:'',
          //编辑表面
          modifyItems:'',
          editVisible2:false,

          ruleItems:[],
          ruleoptions:[{
                id:1,
                name:'加工优先级'
            },{
                id:2,
                name:'加工方法'
            }
          ],
          editVisiblerule:false,
          newruletype:'',
          newtext:'',
          editVisiblerule2:false,
          modifyruleItem:''
        };
    },
    mounted(){
        let {id} = this.$route.params;
        this.projectId = id;
        this.Finishedsurface();
        this.Listrules()
    },
    methods: {
        async Listrules() {//得到规则列表
          this.loading = true;
          try {
            let response = await fetchListrules(this.projectId, 1, 50);
            if (response.status.code ==0) {
                this.ruleItems = response.result;
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            this.loading = false;
          }
      },
      async newrule(){
        let {newtext, newruletype, projectId} = this;
        if (!newruletype) {this.$message.error('类型不能为空');return};
        if (!newtext) {this.$message.error('规则不能为空');return};
         const loading = this.$loading({
          lock: true,
          text: '加载中...',
          spinner: 'el-icon-loading'
        });
        try {
          let response = await fetchAddrule(projectId, newruletype, newtext);
          if (response.status.code ==0) {
            this.$message({
              message: '添加成功',
              type: 'success'
            });
            this.editVisiblerule = false;
            this.Listrules()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          loading.close()
        }
      },
      delrule(id){
        this.$confirm('确定要删除吗？', '提示', {
            type: 'warning'
        })
        .then(() => {
            this.Deleterule(id)
        })
        .catch(() => {});
      },
      async Deleterule(id) {//删除表面
          const loading = this.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading'
          });
          try {
            let response = await fetchDeleterule(id);
            if (response.status.code ==0) {
              this.$message({
                message: '删除成功',
                type: 'success'
              });
              this.Listrules();
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            loading.close()
          }
      },
      editrule(item){
        this.editVisiblerule2=true;
        this.modifyruleItem=Object.assign({}, item);
      },
      async modifyrule(){
        let {modifyruleItem, projectId} = this;
        if (!modifyruleItem.rulesType) {this.$message.error('类型不能为空');return};
        if (!modifyruleItem.rulesDetails) {this.$message.error('规则不能为空');return};
         const loading = this.$loading({
          lock: true,
          text: '加载中...',
          spinner: 'el-icon-loading'
        });
        try {
          let response = await fetchUpdaterule(modifyruleItem.id, projectId, modifyruleItem.rulesType, modifyruleItem.rulesDetails);
          if (response.status.code ==0) {
            this.$message({
              message: '修改成功',
              type: 'success'
            });
            this.editVisiblerule2 = false;
            this.Listrules()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          loading.close()
        }
      },
      delbiao(id){
        this.$confirm('确定要删除吗？', '提示', {
            type: 'warning'
        })
        .then(() => {
            this.Finishedsurfacedel(id)
        })
        .catch(() => {});
      },
      async Finishedsurfacedel(id) {//删除表面
        let {projectId} = this;
          const loading = this.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading'
          });
          try {
            let response = await fetchFinishedsurfacedel(projectId, id);
            if (response.status.code ==0) {
              this.$message({
                message: '删除成功',
                type: 'success'
              });
              this.Finishedsurface();
            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            loading.close()
          }
      },
      sortId(a,b){
          return a.surfaceId - b.surfaceId
        },
      async Finishedsurface() {//得到表面列表
          this.loading = true;
          try {
            let response = await fetchFinishedsurface(this.projectId);
            if (response.status.code ==0) {
                var result = response.result.list;
                result.sort(this.sortId);
              this.biaolist = result

            }else{
              this.$message.error(response.status.description);
            }
          } catch (e) {
            console.log(e);
          } finally {
            this.loading = false;
          }
      },
      
      editbiao(item){//编辑打开弹框
        this.editVisible2=true;
        this.modifyItems=Object.assign({}, item);
      },
      async modifybiao(){//修改表面
        let {modifyItems, projectId} = this;
        if (!modifyItems.name) {this.$message.error('名称不能为空');return};
        if (!modifyItems.type) {this.$message.error('类型不能为空');return};
         const loading = this.$loading({
          lock: true,
          text: '加载中...',
          spinner: 'el-icon-loading'
        });
        try {
          let response = await fetchFinishedsurfaceupdate(projectId, modifyItems.surfaceId, modifyItems.type, modifyItems.name);
          if (response.status.code ==0) {
            this.$message({
              message: '修改成功',
              type: 'success'
            });
            this.editVisible2 = false;
            this.Finishedsurface()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          loading.close()
        }
      },
      async newbiao(){//新建表面
        let {newname, newtype, projectId} = this;
        if (!newname) {this.$message.error('名称不能为空');return};
        if (!newtype) {this.$message.error('类型不能为空');return};
        if(this.biaolist.length==0){
            var surfaceId =1;
        }else if (this.biaolist.length==this.biaolist[this.biaolist.length-1].surfaceId) {
            var surfaceId = this.biaolist.length+1;
        }else{

            var newarray = this.biaolist.map(b => b.surfaceId);
            var surfaceId;
            for(var a=0;a<Math.max(...newarray);a++){
                if (newarray.indexOf(a+1)==-1) {
                    surfaceId=a+1;
                    break;
                };
            }
        }
         const loading = this.$loading({
          lock: true,
          text: '加载中...',
          spinner: 'el-icon-loading'
        });
        try {
          let response = await fetchFinishedsurfaceadd(projectId, surfaceId, newtype, newname);
          if (response.status.code ==0) {
            this.$message({
              message: '添加成功',
              type: 'success'
            });
            this.editVisible = false;
            this.Finishedsurface()
          }else{
            this.$message.error(response.status.description);
          }
        } catch (e) {
          console.log(e);
        } finally {
          loading.close()
        }
      },
    }
};
</script>
<style lang='scss'>
  .cont{
      tbody{
        .el-table__expanded-cell{
          padding:5px 0 5px 60px;
        }
      }
    }
</style>
<style lang='scss' scoped>
    .red{
      color:red;
      font-size:12px;
      line-height:16px;
      padding-top:5px;
    }
    .cont{
      padding-top:20px;
      .class-bg{
        overflow:hidden;
        line-height:32px;
        font-size:13px;
        color:#666;
      }
      .college{
        margin-right:-10px;
      }
    }
    .pagination{
        margin-top:20px;
    }
    .buttonbg{
      text-align:center;
      padding-top: 20px;
    }
</style>


